import { Component, OnInit, Output, EventEmitter, ViewChild, TemplateRef, HostBinding, ViewEncapsulation, ElementRef, Input } from '@angular/core';
import { NotifyService } from '@farris/ui-notify';
import { QuerySolutionVo, SolutionService } from '@farris/component-querycondition';
import { LocaleService } from '@farris/ui-locale';
import { DialogComponent } from '@farris/ui-dialog';
import { cloneDeep } from 'lodash-es';

@Component({
  selector: 'app-saveasdialog',
  templateUrl: './saveasdialog.component.html',
  styleUrls: ['./saveasdialog.component.css'],
  encapsulation: ViewEncapsulation.None
})
export class SaveAsDialogComponent implements OnInit {

  @HostBinding('class') cls = 'saveasdialog';
  @Output() cancelEmitter: EventEmitter<any> = new EventEmitter<any>();
  @Output() confirmEmitter: EventEmitter<any> = new EventEmitter<any>();
  //@Input
  querySolution: QuerySolutionVo = new QuerySolutionVo();
  //@Input
  title: string;
  //
  defaultQuerySolution: QuerySolutionVo;

  @ViewChild('dialog') dialog: DialogComponent;

  constructor(private notify: NotifyService, private solutionService: SolutionService, private localeService: LocaleService) { }


  ngOnInit() {

  }

  show(){
    this.dialog.show();
  }

  cancel() {
    // this.cancelEmitter.emit(true);
    this.dialog.close();
  }

  confirm() {
    if (!this.querySolution.code) {
      this.solutionService.showInfo(this.localeService.getValue('querySolution.saveAsDialog.pleaseInput'), 'info', '', 1500);
      return;
    }
    let obj = new Object();
    obj['querySolution'] = cloneDeep(this.querySolution);
    this.confirmEmitter.emit(obj);
    this.dialog.close();
  }

  setDefault(checkStatus: boolean) {
    this.querySolution.isDefault = checkStatus;
  }
}
