import { Component, OnInit, Input,  ViewChild, ElementRef, NgZone, OnDestroy, HostBinding, AfterViewInit, ChangeDetectorRef } from '@angular/core';
import { QueryCondition} from '@farris/component-querycondition';
import ResizeObserver from "resize-observer-polyfill";
@Component({
  selector: 'lib-summary-list',
  templateUrl: './summary-list.component.html',
  styleUrls: ['./summary-list.component.css']
})
export class SummaryListComponent implements OnInit, OnDestroy {
    
  @HostBinding('class.summary-list') cls=true;
  @Input() conditions: Array<QueryCondition> = new Array<QueryCondition>();
  @Input() enableTips = false;
  @ViewChild('wrapper', { read: ElementRef }) wrapperEl: ElementRef;
  private ro: ResizeObserver | null = null;
  widthFlag = 0;
  constructor(private ngZone:NgZone,private cd: ChangeDetectorRef) {

  }
  ngOnDestroy(): void {
    if (this.ro) {
      this.ro.unobserve(this.wrapperEl.nativeElement);
      this.ro = null;
    }
  }

  ngOnInit() {
    this.observerElement();
  }

  private observerElement() {
    this.ngZone.runOutsideAngular(() => {
      this.ro = new ResizeObserver((entries, observer) => {
        const tempWidth = entries[0].contentRect.width;
        // 80
        if (Math.abs(tempWidth - this.widthFlag) > 15) {
          this.widthFlag = tempWidth;
          this.changeTipsState();
        }
      });
      this.ro.observe(this.wrapperEl.nativeElement);
    });
  }
  private changeTipsState() {
    let tEnableTips=this.enableTips;
    if (this.wrapperEl.nativeElement.clientWidth +2 < this.wrapperEl.nativeElement.scrollWidth) {
      tEnableTips = true;
    } else {
      tEnableTips = false;
    }
    if(tEnableTips!==this.enableTips){
      this.enableTips=tEnableTips;
      this.cd.markForCheck();
      this.cd.detectChanges();
    }
  }
}
